import React, { useState, useEffect } from 'react'
import { Alert, Empty, Space, Spin } from 'antd'
import { User } from '@models'
import { userServices } from '@services'
import UserCard from '@components/UserCard'

import styles from './UserList.module.less'

interface UserListProps {
  className?: string
}

interface UserListState {
  isLoading: boolean
  error?: Error
  users?: User[]
}

const UserList = ({ className }: UserListProps) => {
  const [userListState, setUserListState] = useState<UserListState>({
    isLoading: true,
    error: undefined,
    users: []
  })

  useEffect(() => {
    setUserListState({ isLoading: true })
    userServices
      .getUsers()
      .then((users) => setUserListState({ isLoading: false, users }))
      .catch((error) => setUserListState({ isLoading: false, error }))
  }, [])

  const showPlaceholder =
    userListState.isLoading || userListState.users?.length === 0

  return (
    <div
      className={[styles.root, className, showPlaceholder && styles.placeholder]
        .filter((x) => !!x)
        .join(' ')}
    >
      {userListState.isLoading && (
        <Spin size="large" aria-label="Loading users" />
      )}

      {userListState.error && (
        <Alert
          type="error"
          className={styles.error}
          message={userListState.error.message}
        />
      )}

      {userListState.users && (
        <>
          {userListState.users.length ? (
            <Space size="middle" wrap aria-label="List of users">
              {userListState.users.map((user) => (
                <UserCard key={user.id} user={user} />
              ))}
            </Space>
          ) : (
            <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} aria-label="No users" />
          )}
        </>
      )}
    </div>
  )
}

export default UserList
